/*button styles*/
.default-btn {
  width: 100%;
  float: center;
  margin-top: 2%;
  margin-bottom: 2%;
  font-weight: 600;
  font-size: 200%;
  border-radius: 8px;
  padding: 5% 0 5% 0;
}

.sub-btn {
  width: 60%;
  float: center;
  margin: auto;
  margin-top: 2%;
  margin-bottom: 2%;
  border-radius: 8px;
}

.center {
  display: flex;
  justify-content: center;
  align-items: center;
}

/*button colours*/
.turquoise {
  background-color: #32797e;
  border-color: #0c656b;
  color: white;
}
.turquoise:hover {
  background-color: #0c656b;
  border-color: white;
  color: white;
}
.turquoise:focus {
  background-color: #0c656b;
  border-color: white;
}
.turquoise:active {
  background-color: #32797e;
  border-color: white;
}

